import payHeader from './base/payHeader.vue';
import popupPay from '../../components/popupPay.vue';
export default {
	data() {
		return {
			goodsInfo: [],
			addressData: {
				name: '',
				mobile: '',
				addressName: '',
				address: '',
				area: '',
				default: '',
			}
		}
	},
	components: {
		payHeader,
		popupPay
	},
	methods: {
		navTo(item) {

			if (item.iconTitle == '去评价') {
				uni.navigateTo({
					url: '../order/base/orderComment?goodsInfo=' + JSON.stringify(item)
				})
			} else if (item.iconTitle == '提醒发货') {
				uni.showLoading({
					title: "发送中..."
				})
				setTimeout(() => {
					uni.hideLoading()
					uni.showToast({
						icon: "success",
						title: "已成功提示商家",
					})
					setTimeout(() => {
						uni.navigateTo({
							url : '../order/order'
						})
					},200)
				}, 1000)
			}
			else if( item.iconTitle == '确认收货'){
				uni.showModal({
					title: '确认收货',
					content: '确定订单吗？',
					cancelText: '否',
					confirmText: '是',
					success: res => {
						setTimeout(() => {
							uni.showToast({
								icon: "success",
								title: "订单已完成",
							})
							setTimeout(() => {
								uni.navigateTo({
									url : '../order/order'
								})
							},500)
						},500)
						// if (res.confirm) {
						// 	this.imgList.splice(e.currentTarget.dataset.index, 1)
						// }
					}
				})
			}
		}
	},
	onLoad(option) {
		// this.addressData = JSON.parse(option.addressData)
		this.goodsInfo = JSON.parse(option.goodsInfo)
		// console.log(this.goodsInfo)
	}
}
